html, body, #root { 
    width: 100%;
    height: 49.74vw; /* height:width ratio = 955/1920 브라우저별로 좀 남을 수 있다 */
    max-width: 201vh; /* 1920/955 */
    max-height: 100%;
    margin: auto;
    position: absolute;
    top: 0; bottom: 0; /* vertical center */
    left: 0; right: 0; /* horizontal center */
}

