.App {
    width: 100%;
    height: 100%;
    color: #FCF6F5FF;
    background-color: var(--theme-page-background);
    /* background-color: #00539CFF; */
    font-family: "Roboto", sans-serif;
    font-family: 'Black Han Sans', sans-serif;
    font-family: 'Fugaz One', cursive;
    font-family: 'Jua', sans-serif;
    font-family: 'Luckiest Guy', cursive;
    font-family: 'Orbitron', sans-serif;
    font-family: 'Press Start 2P', cursive;
    font-family: 'Stylish', sans-serif;
    font-family: 'Poor Story', cursive;
    font-family: 'Sunflower', sans-serif;
    font-family: 'Texturina', serif;
}

.App:after {
    content: "";
    top: -100%;
    left: -50%;
    width: 300%;
    height: 300%;
    position: fixed;
    background-image: url(/portfolio/static/media/background.0dc0248a.png);
    opacity: var(--theme-page-opacity);
    -webkit-animation: universe 30s steps(11) infinite;
            animation: universe 30s steps(11) infinite;
    z-index: 2;
}

@-webkit-keyframes universe {
    0% { transform: translate(0, 0) }
    10% { transform: translate(-5%, 5%) }
    20% { transform: translate(10%, -5%) }
    30% { transform: translate(5%, 10%) }
    40% { transform: translate(-10%, 5%) }
    50% { transform: translate(5%, 10%) }
    60% { transform: translate(10%, -5%) }
    70% { transform: translate(-10%, 0%) }
    80% { transform: translate(5%, -5%) }
    90% { transform: translate(-15%, 10%) }
    100% { transform: translate(5%, 15%) }
}

@keyframes universe {
    0% { transform: translate(0, 0) }
    10% { transform: translate(-5%, 5%) }
    20% { transform: translate(10%, -5%) }
    30% { transform: translate(5%, 10%) }
    40% { transform: translate(-10%, 5%) }
    50% { transform: translate(5%, 10%) }
    60% { transform: translate(10%, -5%) }
    70% { transform: translate(-10%, 0%) }
    80% { transform: translate(5%, -5%) }
    90% { transform: translate(-15%, 10%) }
    100% { transform: translate(5%, 15%) }
}
.light {
    --theme-page-background: #9CC3D5FF;
    --theme-page-opacity: 1;
}

.dark {
    --theme-page-background: rgb(14, 69, 114);
    --theme-page-opacity: 0.5;
}
.portfolio_toggle {
    top: 538px;
    left: 260px;
    width: 70px;
    height: 50px;
    position: absolute;
    z-index: 30;
}

.portfolio_toggle input {
    /* input 박스 */
    display: none;
    position: absolute;
}

.portfolio_toggle label {
    display: block;
    position: relative;
    width: 70px;
    height: 50px;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
}

.portfolio_toggle label:hover {
    cursor: url(/portfolio/static/media/cursor_2.b642a301.cur), auto;
}

.portfolio_toggle label:after {
    /* 동그라미 */
    content:""; 
    position: absolute;  
    width: 70px; 
    height: 50px; 
}

.portfolio_toggle input:checked + label { 
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    margin-left: 13px;
    /* transition: all 0.1s; */
}

